import React from "react";
import { MDBBtn, MDBIcon, MDBBtnGroup } from "mdbreact";
import ReactDOM from 'react-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee } from '@fortawesome/free-solid-svg-icons'
import "./Contacts.css";
import "./additional_css/Socicons.css"

export const Contact = () => (
  <>
    <div className="contactpage-wrapper">
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css" />
      {/* hero image */}
      <section>
        <div className="contactpage-heroimage"></div>
      </section>
      {/* contact form and social icons */}
      <section id="contact-form-and-social-icons">
        <div className="contactpage-contact-form-wrapper">
          <div className="contact-form-wrapper">
            <div className="container contact-form">
              <div className="headline-header-title text-center">
                <h2>Feel free to contact us!</h2>
              </div>
              <div className="row">
              <div className="col-12 col-sm-12 col-md-12 col-lg-3 col-xl-3"></div>
              <div className="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6">
              <div className="contact-form-inner border border-light">
                <form style={{margin: "1rem"}}>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="inputEmail4">Email</label>
                      <input type="email" class="form-control" id="inputEmail4" placeholder="Email" />
                    </div>
                    <div class="form-group col-md-6">
                      <label for="inputPassword4">Password</label>
                      <input type="password" class="form-control" id="inputPassword4" placeholder="Password" />
                    </div>
                  </div>
                  <div class="form-group">
                    <label for="inputAddress">Address</label>
                    <input type="text" class="form-control" id="inputAddress" placeholder="1234 Main St" />
                  </div>
                  <div class="form-group">
                    <label for="inputAddress2">Address 2</label>
                    <input type="text" class="form-control" id="inputAddress2" placeholder="Apartment, studio, or floor" />
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="inputCity">City</label>
                      <input type="text" class="form-control" id="inputCity" />
                    </div>
                    <div class="form-group col-md-4">
                      <label for="inputState">State</label>
                      <select id="inputState" class="form-control">
                        <option selected>Choose...</option>
                        <option>...</option>
                      </select>
                    </div>
                    <div class="form-group col-md-2">
                      <label for="inputZip">Zip</label>
                      <input type="text" class="form-control" id="inputZip" />
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      <input class="form-check-input" type="checkbox" id="gridCheck" />
                      <label class="form-check-label" for="gridCheck">
                        Check me out
                      </label>
                    </div>
                  </div>
                  <div className="form-button text-center">
                    <button type="submit" class="btn btn-primary">Message Us</button>
                  </div>
                </form>
                <div className="socicons-wrapper text-center">
                  <a href="#" className="fa fa-facebook"></a>
                  <a href="#" className="fa fa-twitter"></a>
                  <a href="#" className="fa fa-google"></a>
                  <a href="#" className="fa fa-youtube"></a>
                  <a href="#" className="fa fa-instagram"></a>
                  <a href="#" className="fa fa-phone"></a>
                </div>
              </div>
              </div>
              <div className="col-12 col-sm-12 col-md-12 col-lg-3 col-xl-3"></div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  </>
);

